.flexinav {
  background: #080808;
  box-shadow: 0 0.125rem 1rem rgba(8, 8, 8, 0.75);
  float: left;
  height: 4rem;
  line-height: 4rem;
  margin: 0;
  padding: 0;
  position: relative;
  width: 100%;
  z-index: 9990; }

@media (min-width: 768px) {
  .flexinav_fixed {
    left: 0;
    position: fixed;
    top: 1.5rem; } }

.flexinav_menu {
  float: left;
  margin: 0 0 0 -1rem; }

.flexinav_menu_right {
  float: right;
  margin: 0 -1rem 0 0; }

.nav-brand {
  font-size: 1.5rem; }

.flexinav_menu > li {
  float: left;
  padding: 0;
  margin: 0;
  position: relative;
  background: #080808;
  transition: background .3s; }
  .flexinav_menu > li.flexnav_mega {
    position: static; }
  .flexinav_menu > li > a,
  .flexinav_menu > li > span,
  .flexinav_menu > li > span > a {
    color: #f9e406; }
  .flexinav_menu > li:hover, .flexinav_menu > li.active {
    background-color: #f9e406; }
    .flexinav_menu > li:hover > a,
    .flexinav_menu > li:hover > span,
    .flexinav_menu > li:hover > span > a, .flexinav_menu > li.active > a,
    .flexinav_menu > li.active > span,
    .flexinav_menu > li.active > span > a {
      color: #080808; }
  .flexinav_menu > li.noactive {
    background-color: #080808; }
    .flexinav_menu > li.noactive > a,
    .flexinav_menu > li.noactive > span,
    .flexinav_menu > li.noactive > span > a,
    .flexinav_menu > li.noactive:hover > a,
    .flexinav_menu > li.noactive:hover > span,
    .flexinav_menu > li.noactive:hover > span > a {
      color: #f9e406; }
  .flexinav_menu > li > a,
  .flexinav_menu > li > span,
  .flexinav_menu > li > span > a {
    cursor: pointer;
    display: block;
    padding: 0 .5rem;
    text-decoration: none; }
    @media (min-width: 992px) {
      .flexinav_menu > li > a,
      .flexinav_menu > li > span,
      .flexinav_menu > li > span > a {
        padding: 0 1rem; } }

.sub-flexinav {
  background-color: #f9e406;
  font-size: .875rem;
  height: 1.5rem;
  line-height: 1.5rem; }
  .sub-flexinav .flexinav_menu {
    float: left;
    margin: 0 0 0 -1rem; }
    .sub-flexinav .flexinav_menu > li {
      background-color: #f9e406;
      font-family: 'AvenirNextLTPro-Light';
      font-style: normal;
      font-weight: normal;
      letter-spacing: 0;
      text-transform: none; }
      .sub-flexinav .flexinav_menu > li a {
        color: #080808;
        padding: 0 1rem; }
        .sub-flexinav .flexinav_menu > li a:hover {
          background-color: #080808;
          color: #f9e406; }

@media (min-width: 768px) {
  .sub-flexinav.flexinav_fixed {
    left: 0;
    position: fixed;
    top: 0; } }

