  margin: 0;
  opacity: 0;
  padding: 1rem .5rem;
  position: absolute;
  right: 0;
  top: -9999em;
  z-index: 9999; }
  .flexinav_ddown a {
    color: #080808; }
    .flexinav_ddown a:hover {
      color: #484848; }

.page-dt2 .flexinav_ddown {
  background: white;
  color: #471110; }
  .page-dt2 .flexinav_ddown a {
