/*  _______________________________________________

    05 MULTILEVEL FLY-OUTS
    _______________________________________________  */
.dropdown_flyout,
.dropdown_flyout .dropdown_flyout_level {
  padding: 0;
  margin: 0;
  list-style: none; }

.dropdown_flyout li {
  float: left;
  width: 100%; }

.dropdown_flyout li a,
.dropdown_flyout li span {
  float: left;
  width: 90%;
  padding: .5rem;
  display: block;
  cursor: pointer; }

/* Fly-Outs */
.dropdown_flyout .dropdown_flyout_level {
  background: #ffffff;
  position: absolute;
  margin: -7px 0 0 0;
  padding: 6px;
  left: 100%;
  top: auto;
  display: none;
  zoom: 1;
  float: left;
  border: 1px solid #dddddd; }

.dropdown_flyout .dropdown_flyout_level_left {
  left: -102%;
  right: 100%; }

.dropdown_flyout .dropdown_flyout_level li {
  width: 180px;
  padding: 0 10px 0 10px; }

.dropdown_flyout li.last {
  margin-bottom: 12px; }

.dropdown_flyout ul.flexinav_icons li {
  margin-left: -12px !important; }

.dropdown_flyout .flyout_heading {
  font-family: Georgia, Times, "Times New Roman", serif;
  color: #383838;
  font-size: 16px;
  width: 80%;
  padding-bottom: 9px;
  margin: 0 10% 9px 10%;
  border-bottom: 1px solid #eeeeee; }

.dropdown_flyout li:hover > .dropdown_flyout_level,
.dropdown_flyout li:hover > .dropdown_flyout_level_left {
  display: block; }

/* Parent Elements */
.dropdown_parent > span:after {
  content: '\f105';
  font-family: FontAwesome;
  float: right;
  padding-top: 1px; }

.dropdown_parent_left > span:after {
  content: '\f104';
  font-family: FontAwesome;
  float: right;
  padding-top: 1px; }

/*  _______________________________________________

    06 TYPOGRAPHY & ELEMENTS
    _______________________________________________  */
/*  _______________________________________________

    07 FORM ELEMENTS
    _______________________________________________  */
.contact_form {
  display: block;
  margin: 0 0 10px 0; }

.contact_form form {
  margin: 0;
  padding: 0;
  line-height: 18px; }

.contact_form label {
  font-size: 12px;
  margin-bottom: 18px;
  float: left;
  width: 100%;
  font-family: 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif; }

.contact_form .form_element {
  color: #000000;
  background-color: #fbfbfb;
  border: 1px solid #dddddd;
  font-family: 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
  font-size: 12px;
  line-height: 10px;
  width: 100%;
  padding: 9px 2% 9px 2%;
  margin: 0 0 18px 0;
  float: left;
  -webkit-transition: background-color 0.3s ease-out;
  -moz-transition: background-color 0.3s ease-out;
  -o-transition: background-color 0.3s ease-out;
  -ms-transition: background-color 0.3s ease-out;
  transition: background-color 0.3s ease-out; }

.contact_form .form_element:hover {
  background-color: #fcfcfc; }

.contact_form .form_element:focus {
  background-color: #ffffff;
  outline: none; }

.contact_form textarea {
  height: 140px;
  resize: none; }

.contact_form .required {
  color: #C00; }

.contact_form .error,
.contact_form .success {
  font-family: 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
  font-size: 12px;
  line-height: 12px;
  float: left;
  width: 100%;
  padding: 9px 15px 9px 15px;
  margin: 0 0 18px 0;
  border: 1px solid rgba(0, 0, 0, 0.15); }

.contact_form .error {
  background: #f7cac4;
  color: #992213; }

.contact_form .success {
  background: #d8f6bc;
  color: #2f530b;
  margin-bottom: 12px; }

.contact_form .button {
  padding: 9px 14px;
  margin: 9px 12px 0 0;
  white-space: nowrap;
  font-family: 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
  font-size: 11px;
  font-weight: bold;
  text-transform: uppercase;
  vertical-align: middle;
  cursor: pointer;
  outline: none;
  border: 1px solid #000000;
  color: #ffffff;
  background-color: #222222;
  text-shadow: 0 1px 1px #000000;
  -webkit-transition: background-color 0.3s;
  -moz-transition: background-color 0.3s;
  -o-transition: background-color 0.3s;
  -ms-transition: background-color 0.3s;
  transition: background-color 0.3s; }

.contact_form .button:hover,
.contact_form .button:focus {
  color: #ffffff;
  background-color: #1A1A1A; }

